import Vue from 'vue'
import moment from 'moment'

// 
Vue.filter('time', value => {
  return moment(value).format('YYYY-MM-DD HH:mm:ss')
})

// 
Vue.filter('RMB', value => {
  return ' ' + parseInt(value).toLocaleString()
})

/*  */
// 
Vue.filter('betType', value => {
  switch (value) {
    case 1:
      return ''
    case 2:
      return ''
    case 3:
      return '-'
    case 4:
      return '-'
    case 5:
      return '-'
    default:
      return ''
  }
})
